import React from 'react';
import './index.scss';
import RSSBandLogo from './RSSBands.svg';
import reactLogo from './logo.svg';

class Header extends React.Component {
  render() {
    return (
      <header>
        <img src={RSSBandLogo} className='Header-logo' alt='logo' />
        <h1>{this.props.headline || 'Enter headline text'}</h1>
        <img src={reactLogo} className='Header-logo Header-anim' alt='logo' />
      </header>
    );
  }
}

export default Header;
