/* eslint-disable react/jsx-no-target-blank */
import React from 'react';
import './index.scss';
import _ from 'lodash';

const getClass = status => {
  switch (status) {
    case 'Checked':
      return 'NotChecked';
    case 'Checking':
      return 'CheckNow';
    case 'ToDo':
      return 'ToDoNow';
    case 'In Progress':
      return 'Progress';
    default:
      return '';
  }
};

const getStatusCells = (tasks, students) => {
  return _.map(tasks, (item, indx) => {
    const taskKey = item.Name.match(/[a-z0-9]/gi)
      .join('')
      .toLowerCase();
    const row = _.map(students, (student, index) => {
      if (student.Tasks && student.Tasks[taskKey]) {
        const title = `Comment: ${
          student.Tasks[taskKey].Comment
        }\nClick to go to PR`;
        return (
          <td
            id={student.Tasks[taskKey].PR}
            className='Checked'
            title={title}
            key={`status-${index}`}
          >
            +
          </td>
        );
      } else if (!student.Tasks) {
        const className = getClass(item.Status);
        return (
          <td className={className} title='Not Exist' key={`status-${index}`}>
            o
          </td>
        );
      } else {
        const className = getClass(item.Status);
        return (
          <td
            className={className}
            title='Mark not found'
            key={`status-${index}`}
          >
            o
          </td>
        );
      }
    });
    return <tr key={`stusrow-${indx}`}>{row}</tr>;
  });
};

const getTasksName = arr => {
  return arr.map((item, index) => {
    const status = item.Status || '';
    const taskClass = status.match(/[a-z]/gi).join('');
    return (
      <tr key={index}>
        <td className={taskClass} title={item.Status}>
          <a href={`${item.Link}`} target='_blank'>
            {item.Name}
          </a>
        </td>
      </tr>
    );
  });
};

const getStudentsName = arr => {
  return arr.map((item, index) => {
    return (
      <th className='Student' key={index}>
        <a href={`https://github.com/${item.Nick}`} target='_blank'>
          {item.Nick}
        </a>
      </th>
    );
  });
};

const goToPR = event => {
  const url = event.target.id;
  if (url) {
    const win = window.open(url, '_blank');
    win.focus();
  }
};

class Table extends React.Component {
  render() {
    if (!this.props.tableData.students) {
      return <div />;
    } else {
      const taskArr = this.props.tableData.tasks;
      const tasks = getTasksName(taskArr);
      const studentsArr = _.sortBy(_.values(this.props.tableData.students), [
        'Tasks',
        'Nick',
      ]);
      const tableBody = getStatusCells(taskArr, studentsArr);
      const studentsName = getStudentsName(studentsArr);
      return (
        <div className='Table'>
          <div>
            <h2>Tasks</h2>
            <table>
              <tbody>
                <tr>
                  <td className='notSeen'>.</td>
                </tr>
                {tasks}
              </tbody>
            </table>
          </div>
          <div>
            <h2>Students</h2>
            <table onClick={goToPR}>
              <thead>
                <tr>{studentsName}</tr>
              </thead>
              <tbody>{tableBody}</tbody>
            </table>
          </div>
        </div>
      );
    }
  }
}

export default Table;
