import React from 'react';
import './index.scss';
import _ from 'lodash';
import Select from 'react-select';
import Table from './table';
import Axios from 'axios';

const options = [];

let students = {};

class Dashboard extends React.Component {
  state = {
    selectedOption: {},
    tableData: {},
    data: {},
    options: [],
    got: false,
  };

  handleChange(selectedOption) {
    students = this.mentors[selectedOption.label].Students;
    const tableData = {
      students: students,
      tasks: this.tasks,
    };
    this.setState({
      ...this.state,
      selectedOption,
      tableData,
    });
    const user = {
      selectedOption: selectedOption,
      tableData: tableData,
    };
    localStorage.setItem('mdLastUserState', JSON.stringify(user));
  }

  updateData = () => {
    this.getData();
    localStorage.removeItem('mdLastUserState');
    window.location.reload(true);
  };

  async getData() {
    const response = await Axios.get('dashboard.json');
    this.mentors = response.data.mentors;
    this.tasks = response.data.tasks || {};
    const listOfMentors = _.sortBy(_.values(this.mentors), item => {
      return item.Nick;
    });

    _.forEach(listOfMentors, mentor => {
      options.push({
        value: mentor.Name,
        label: mentor.Nick,
      });
    });
    this.setState({
      ...this.state,
      options: options,
      got: true,
    });
  }

  componentDidMount() {
    if (!this.state.got) {
      this.getData();
    }
    const user = JSON.parse(localStorage.getItem('mdLastUserState'));
    if (user) {
      const selectedOption = user.selectedOption;
      const tableData = user.tableData;
      const state = user.state;
      this.setState({
        ...this.state,
        selectedOption,
        tableData,
        state,
      });
    }
  }

  render() {
    const { selectedOption, tableData } = this.state;
    const name = selectedOption.value || '';
    return (
      <div className='Dashboard'>
        <h2>
          Mentor:{' '}
          <a
            href={`https://github.com/${selectedOption.label}`}
            target='_blank'
            rel='noopener noreferrer'
          >
            {name}
          </a>
        </h2>
        <Select
          id='sel'
          className='ChooseMentor'
          value={selectedOption}
          onChange={this.handleChange.bind(this)}
          options={this.state.options}
        />
        <Table tableData={tableData} />
        <button className='btn btn-success mt-2' onClick={this.updateData}>
          UPDATE
        </button>
      </div>
    );
  }
}

export default Dashboard;
